import React, { Fragment } from "react";
import Navbar from "./Navbar";
import Prefooter from "./Prefooter";
import Footer from "./Footer";

const About = () => {
  return (
    <Fragment>
      <Navbar />
      <div className="container">
        <div className="my-3">
          <h1 className="heading-green">
            Proto Solutions Inc была основана в 2020 году четырьмя начинающими
            разработчиками в городе Макати.
          </h1>
          <p>
            Наше приложение помогает людям заказать еду, такси, оплатить счета,
            получить удивительные скидки и купоны на их следующий шоппинг или
            ужин.
          </p>
        </div>
        <div>
          <p>
            Давно выяснено, что при оценке дизайна и композиции читаемый текст
            мешает сосредоточиться.{" "}
          </p>
          <p>
            Lorem Ipsum используют потому, что тот обеспечивает более или менее
            стандартное заполнение шаблона, а также реальное распределение букв
            и пробелов в абзацах, которое не получается при простой дубликации
            "Здесь ваш текст.. Здесь ваш текст.. Здесь ваш текст.."
          </p>{" "}
          <p>
            Многие программы электронной вёрстки и редакторы HTML используют
            Lorem Ipsum в качестве текста по умолчанию, так что поиск по
            ключевым словам "lorem ipsum" сразу показывает, как много
            веб-страниц всё ещё дожидаются своего настоящего рождения.
          </p>{" "}
          <p>
            За прошедшие годы текст Lorem Ipsum получил много версий. Некоторые
            версии появились по ошибке, некоторые - намеренно (например,
            юмористические варианты).
          </p>
        </div>
      </div>
      <Prefooter />
      <Footer />
    </Fragment>
  );
};

export default About;
