import React from "react";
import { Link } from "react-router-dom";

import iosBtnImg from "../../assets/img/logos/app-store-btn.png";
import androidBtnImg from "../../assets/img/logos/play-store-btn.png";

const Footer = () => {
  return (
    <footer className="main-footer">
      <div>
        <Link className="footer-link" to="/terms-conditions">
          Kонфиденциальност
        </Link>
        <Link className="footer-link" to="/terms-conditions">
          Условия пользования
        </Link>
      </div>
      <div>
        <p className="footer-p-text">&copy; Copyright 2020</p>
      </div>
      <div>
        <button className="footer-getapp-btn mx-1">
          <img src={iosBtnImg} className="img-fluid" />
        </button>
        <button className="footer-getapp-btn mx-1">
          <img src={androidBtnImg} className="img-fluid" />
        </button>
      </div>
    </footer>
  );
};

export default Footer;
