import React, { Fragment, useState } from "react";

import Navbar from "./layout/Navbar";
import Prefooter from "./layout/Prefooter";
import Footer from "./layout/Footer";

import callIcon from ".././assets/img/icons/GetStarted-Call.png";
import collectionIcon from ".././assets/img/icons/GetStarted-Collection-1.png";
import onlinetrainingIcon from ".././assets/img/icons/GetStarted-OnlineTraining.png";
import signupIcon from ".././assets/img/icons/GetStarted-SignUp.png";

import whyBeIcon from ".././assets/img/icons/WhyBe-1.png";
import whyBe2Icon from ".././assets/img/icons/WhyBe-2-1.png";

const BecomeMerchantRegistration = () => {
  const [businessName, setBusinessName] = useState("");

  const [name, setName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");
  const [city, setCity] = useState("");

  const onBussinessNameChange = (e) => {
    setBusinessName(e.target.value);
  };

  const onNameChange = (e) => {
    setName(e.target.value);
  };

  const onLastnameChange = (e) => {
    setLastName(e.target.value);
  };

  const onEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const onPasswordChange = (e) => {
    setPassword(e.target.value);
  };
  const onPhoneChange = (e) => {
    setPhone(e.target.value);
  };
  const onCityChange = (e) => {
    setCity(e.target.value);
  };

  const onSubmit = (e) => {
    e.preventDefault();
    const data = {
      registerMerchantData: {
        businessName,
        name,
        lastName,
        email,
        password,
        phone,
        city,
      },
    };
    console.log(data);
    clearFields();
  };
  const clearFields = () => {
    setBusinessName("");
    setName("");
    setLastName("");
    setEmail("");
    setPassword("");
    setCity("");
    setPhone("");
  };
  return (
    <Fragment>
      <Navbar />
      <section id="driver-register-form" className="driver-register-wrapper">
        <div
          id="driver-register-form-wrapper"
          className="container-fluid p-4 p-md-3 p-lg-5 p-xl-5 d-flex justify-content-around"
        >
          <div id="becomedriver-heading-wrapper" className="w-25 pt-5">
            <div className="mb-3 mb-md-0 mb-lg-0 mb-xl-0">
              <h1 className="text-black">
                Больше посетителей чтобы удовлетворить, больше бизнеса для вас.
              </h1>
              <h3>
                Развивайте свой бизнес, доставляя еду на столы голодных
                клиентов.
              </h3>
            </div>
          </div>
          <div className="card w-55 p-2 p-md-3 p-lg-3 p-xl-5">
            <form className="p-3 p-md-0 p-lg-0 p-xl-0" onSubmit={onSubmit}>
              <div className="my-3">
                <h4 className="heading-green">
                  Регистрация ProtoFood Торговец-партнерa!
                </h4>
                <p className="heading-green">
                  Введите данные вашего ресторана, чтобы начать.
                </p>
              </div>
              <div className="form-group">
                <label htmlFor="inputPhone">Название вашего бизнеса</label>
                <input
                  type="text"
                  className="form-control"
                  id="inputPhone"
                  placeholder="название бизнеса..."
                  value={businessName}
                  onChange={onBussinessNameChange}
                />
              </div>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <label htmlFor="inputName4">Имя</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputName4"
                    placeholder="Имя"
                    value={name}
                    onChange={onNameChange}
                  />
                </div>
                <div className="form-group col-md-6">
                  <label htmlFor="inputLastname4">Фамилия</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputLastname4"
                    placeholder="Фамилия"
                    value={lastName}
                    onChange={onLastnameChange}
                  />
                </div>
              </div>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <label htmlFor="inputEmail4">Электронная почта</label>
                  <input
                    type="email"
                    className="form-control"
                    id="inputEmail4"
                    placeholder="@почта"
                    value={email}
                    onChange={onEmailChange}
                  />
                </div>
                <div className="form-group col-md-6">
                  <label htmlFor="inputPassword4">Пароль</label>
                  <input
                    type="password"
                    className="form-control"
                    id="inputPassword4"
                    placeholder="пароль"
                    value={password}
                    onChange={onPasswordChange}
                  />
                </div>
              </div>
              <div className="form-group">
                <label htmlFor="inputPhone">Номер телефона</label>
                <input
                  type="text"
                  className="form-control"
                  id="inputPhone"
                  placeholder="телефон"
                  value={phone}
                  onChange={onPhoneChange}
                />
              </div>

              <div className="input-group mb-3">
                <select
                  className="custom-select"
                  id="inputGroupSelect02"
                  value={city}
                  onChange={onCityChange}
                >
                  <option defaultValue>Выбираете...</option>
                  <option value="Ташкент">Ташкент</option>
                  <option value="Самарканд">Самарканд</option>
                  <option value="Андижан">Андижан</option>
                </select>
                <div className="input-group-append">
                  <label
                    className="input-group-text"
                    htmlFor="inputGroupSelect02"
                  >
                    Город
                  </label>
                </div>
              </div>
              <div className="form-group">
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    id="gridCheck"
                    required={true}
                  />
                  <label className="form-check-label" htmlFor="gridCheck">
                    Я прочитал, понимаю и согласен с{" "}
                    <a href="/terms">политикой конфиденциальности Proto</a>
                  </label>
                </div>
              </div>
              <button type="submit" className="btn btn-block btn-success">
                Зарегистрироваться
              </button>
            </form>
          </div>
        </div>
        <div
          id="partner-driver-heading-whyjoin-wrapper"
          className="container w-100 p-2 p-md-5 p-lg-5 p-xl-5"
        >
          <div>
            <h2 className="partner-driver-heading mb-3 mb-xl-5">
              Зачем быть торговым партнером ProtoFood?
            </h2>

            <div
              id="whybemerchant-wrapper"
              className="d-flex justify-content-center"
            >
              <div className="text-center mx-1">
                <img
                  src={whyBeIcon}
                  style={{ maxWidth: "15rem" }}
                  alt="img"
                  className="img-fluid"
                />
                <h3 className="my-3 heading-green">
                  Рынок для наших клиентов.
                </h3>
                <p>
                  Развивайте свой бизнес, используя маркетинговые каналы, чтобы
                  охватить миллионы голодных клиентов Proto.
                </p>
              </div>
              <div className="text-center mx-1">
                <img
                  src={whyBe2Icon}
                  style={{ maxWidth: "15rem" }}
                  alt="img"
                  className="img-fluid"
                />
                <h3 className="my-3 heading-green">Ничего лишнего</h3>
                <p>
                  Вы получаете собственную службу доставки, не прибавляя к своей
                  численности.
                </p>
              </div>
            </div>
          </div>
        </div>
        <div>
          <div className="container p-3 p-sm-3 p-md-3 p-lg-5 p-xl-5">
            <div className="row">
              <div className="col-12 mx-auto col-sm-6 col-md-3 col-lg-3 col-xl-3">
                <img src={signupIcon} className="img-fluid" alt="img" />
                <h4>Шаг 1</h4>
                <p>Зарегистрируйтесь и отправьте свои данные.</p>
                <div>
                  <a
                    className="btn btn-success"
                    href="#becomedriver-heading-wrapper"
                  >
                    Зарегистрируйтесь сейчас
                  </a>
                </div>
              </div>
              <div className="col-12 mx-auto col-sm-6 col-md-3 col-lg-3 col-xl-3">
                <img src={callIcon} className="img-fluid" alt="img" />
                <h4>Шаг 2</h4>
                <p>
                  Наш представитель свяжется с вами для завершения регистрации.
                </p>
              </div>
              <div className="col-12 mx-auto col-sm-6 col-md-3 col-lg-3 col-xl-3">
                <img src={onlinetrainingIcon} className="img-fluid" alt="img" />
                <h4>Шаг 3</h4>
                <p>
                  Отправьте в своем последнем меню, мы заполним ваш
                  интернет-магазин.
                </p>
              </div>
              <div className="col-12 mx-auto col-sm-6 col-md-3 col-lg-3 col-xl-3">
                <img src={collectionIcon} className="img-fluid" alt="img" />
                <h4>Шаг 4</h4>
                <p>Начните обслуживать своих клиентов.</p>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Prefooter />
      <Footer />
    </Fragment>
  );
};

export default BecomeMerchantRegistration;
