import React from "react";
import { Link } from "react-router-dom";

import { FaArrowRight } from "react-icons/fa";

import deliveryLogo from "../../assets/img/icons/covid-delivery-partner-icon.png";
import merchantLogo from "../../assets/img/icons/covid-merchant-partner-icon.png";

import foodDelImage from "../../assets/img/photos/covid-image-food-delivery-new.png";
import deliveryImage from "../../assets/img/photos/covid-image-delivery-services.png";

const ShowCase = () => {
  return (
    <section id="showcase-section" className="mx-auto my-auto p-5">
      <div className="container mx-auto">
        <h2 className="showcase-big-heading my-5 my-xs-1">
          Несмотря на эти неопределенные времена, мы все еще здесь, чтобы помочь
          вам!
        </h2>
        <div className="my-5">
          <div className="row d-flex justficy-content-center">
            <div className="col-12 mx-auto col-md-6 col-lg-6 col-xl-6 d-flex flex-column p-3">
              <div
                id="order-throught-website-container"
                className="d-flex justify-content-around m-2"
              >
                <div className="w-40 mx-2">
                  <p className="food-delivery-p-logo text-white">
                    ДОСТАВКА ЕДЫ
                  </p>
                </div>
                <div className="w-40 mx-2">
                  <p className="text-white">
                    Получите комфортную еду из любимого ресторана, не выходя из
                    дома. Заказать через приложение PROTO, или нажмите ниже,
                    чтобы заказать на нашем сайте.
                  </p>

                  <Link to="/services" className="showcase-container-link">
                    <span className="mr-2">
                      <FaArrowRight color="white" />
                    </span>
                    Заказать через сайт
                  </Link>
                </div>
              </div>
              <div className="align-self-center text-center mt-3">
                <img
                  src={foodDelImage}
                  className="img-fluid"
                  id="showcase-1st-image"
                />
              </div>
            </div>
            <div className="col-12 mx-auto col-md-6 col-lg-6 col-xl-6 d-flex flex-column p-3">
              <div className="align-self-center">
                <div className="my-3">
                  <Link to="/website-order" style={{ textDecoration: "none" }}>
                    <span>
                      <img
                        src={deliveryLogo}
                        className="rounded-circle mx-2"
                        style={{ width: "3rem" }}
                      />
                    </span>
                    <span className="showcase-container-link">
                      Хотите поехать с нами?
                    </span>
                  </Link>
                </div>
                <div className="my-3">
                  <Link to="/website-order" style={{ textDecoration: "none" }}>
                    <span>
                      <img
                        src={merchantLogo}
                        className="rounded-circle mx-2"
                        style={{ width: "3rem" }}
                      />
                    </span>

                    <span className="showcase-container-link">
                      Вы владелец ресторана?
                    </span>
                  </Link>
                </div>
              </div>

              <div
                id="showcase-order-through-app-container"
                className="d-flex justify-content-around mt-5 m-2"
              >
                <div className="w-40 mx-2">
                  <p className="food-delivery-p-logo text-white">
                    УСЛУГИ ДОСТАВКИ
                  </p>
                </div>
                <div className="w-40 mx-2">
                  <p className="text-white">
                    Нужна помощь для безопасной и быстрой отправки или получения
                    посылок? PROTOExpress все еще доступен.
                  </p>

                  <Link to="/website-order" className="showcase-container-link">
                    <span className="mr-2">
                      <FaArrowRight color="white" />
                    </span>
                    скачать приложение!
                  </Link>
                </div>
              </div>
              <div className="align-self-center text-center mt-3">
                <img src={deliveryImage} className="img-fluid" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

/**
 *  


                


            


         
 * 

   
 * 
 * 
 * 
 * 
 * 
 */

export default ShowCase;
