import React, { useState, useEffect, Fragment } from "react";
import Navbar from "./layout/Navbar";
import Prefooter from "./layout/Prefooter";
import Footer from "./layout/Footer";
import DowloadApp from "./layout/DownloadApp";

import carImg from "../assets/img/icons/grabcar.png";
import taxiImg from "../assets/img/icons/standard–taxi.png";
import desktopIcon1 from "../assets/img/icons/DesktopContent1.png";
import desktopIcon3 from "../assets/img/icons/DesktopContent3.png";
import protopayIcon from "../assets/img/icons/protoPayIcon.png";
import protopayIcon2 from "../assets/img/icons/protoPayIcon2.png";
import safetyIcon from "../assets/img/icons/icon-safety-3.png";
import speedIcon from "../assets/img/icons/icon-speed-1.png";
import fingerClickIcon from "../assets/img/icons/one-finger-click.png";
import colockIcon from "../assets/img/icons/clock-1.png";

const SpecificService = ({ match }) => {
  const [info, setInfo] = useState({});
  const [image1, setImage1] = useState("");
  const [image2, setImage2] = useState("");
  const [imgstyle, setImgStyle] = useState({});
  const [imgstyle2, setImgStyle2] = useState({});

  useEffect(() => {
    const dataType = match.params.type;
    switch (dataType) {
      case "cab":
        setInfo({
          heading: "Наши транспортные услуги",
          smallHeading: "Такси",
          smallHeadingP: "Получить такси прямо от вашего порога.",
          smallHeading2: "Личный автомобиль",
          smallHeading2P: "Частные автомобили для ваших повседневных нужд.",
        });
        setImage1(carImg);
        setImage2(taxiImg);
        setImgStyle({ maxWidth: "9.375rem", maxHeight: "9.375rem" });

        break;
      case "food":
        setInfo({
          heading: "Ваше любимое блюдо доставлено горячим и свежим",

          smallHeading: "На расстоянии одного касания от вас.",
          smallHeadingP:
            "Накормите свой голод любимыми блюдами, доставленными вам, где бы вы ни находились.",
          smallHeading2: "Пища для всех пристрастий.",
          smallHeading2P:
            "От местного тарифа до фаворитов ресторана, наш широкий выбор блюд наверняка удовлетворит все ваши желания.",
        });
        setImage1(desktopIcon1);
        setImage2(desktopIcon3);
        setImgStyle({ maxWidth: "10rem" });
        setImgStyle2({ maxWidth: "10rem" });

        break;
      case "pay":
        setInfo({
          heading: "Устали от мелочей каждый раз, когда вы делаете платеж?",
          heading2:
            "Сделайте его еще более полезным с ProtoPay - мобильным кошельком, который вы можете использовать, чтобы беспрепятственно оплачивать услуги Proto и делать больше с вашим приложением Proto!",

          smallHeading: "Оплата услуг Прото и многое другое.",
          smallHeadingP:
            "нет необходимости иметь дело с наличными деньгами и мелочами",
          smallHeading2: "Зарабатывайте очки ProtoRewards.",
          smallHeading2P:
            "для ProtoFood, в магазине и онлайн-транзакций, совершаемых с помощью ProtoPay.",
        });

        setImage1(protopayIcon);
        setImage2(protopayIcon2);
        setImgStyle({ maxWidth: "10rem" });
        setImgStyle2({ maxWidth: "10rem" });
        break;
      case "delivery":
        setInfo({
          heading: "Почему вы должны использовать Proto Delivery?",
          heading2:
            "Получите ваши посылки по запросу с отслеживанием в реальном времени",

          smallHeading: "УДОБСТВО",
          smallHeadingP:
            "Освободите себя от неприятных ощущений, потому что вы оставили что-то дома.",
          smallHeading2: "СКОРОСТЬ",
          smallHeading2P:
            "Освободите себя от ожидания дня, чтобы получить вашу посылку, GrabExpress по запросу, что означает, что ваша посылка будет немедленно забрана и доставлена прямо вашему получателю.",
        });

        setImage1(safetyIcon);
        setImage2(speedIcon);
        setImgStyle({ maxWidth: "7rem" });
        setImgStyle2({ maxWidth: "7rem" });
        break;
      case "airtime":
        setInfo({
          heading: "Устали от мелочей каждый раз, когда вы делаете платеж?",
          heading2:
            "Сделайте его еще более полезным с ProtoPay - мобильным кошельком, который вы можете использовать, чтобы беспрепятственно оплачивать услуги Proto и делать больше с вашим приложением Proto!",

          smallHeading: "Оплата услуг Прото и многое другое.",
          smallHeadingP:
            "нет необходимости иметь дело с наличными деньгами и мелочами",
          smallHeading2: "Зарабатывайте очки ProtoRewards.",
          smallHeading2P:
            "для ProtoFood, в магазине и онлайн-транзакций, совершаемых с помощью ProtoPay.",
        });

        setImage1(protopayIcon);
        setImage2(protopayIcon2);
        setImgStyle({ maxWidth: "10rem" });
        setImgStyle2({ maxWidth: "10rem" });
        break;
      case "bills":
        setInfo({
          heading: "Зачем оплачивать свои счета с ProtPay?",
          heading2: "",

          smallHeading: "Пропустить очереди.",
          smallHeadingP:
            "Оплачивайте свои счета, не выходя из собственного дома или по дороге на работу. Когда угодно, где угодно.",
          smallHeading2: "Легко.",
          smallHeading2P:
            "Мы сохраняем данные вашего биллера в приложении, поэтому вам не нужно будет вводить их повторно при следующей оплате счета",
        });

        setImage1(colockIcon);
        setImage2(fingerClickIcon);
        setImgStyle({ maxWidth: "10rem" });
        setImgStyle2({ maxWidth: "10rem" });
        break;
      case "rewards":
        setInfo({
          heading: "Устали от мелочей каждый раз, когда вы делаете платеж?",
          heading2:
            "Сделайте его еще более полезным с ProtoPay - мобильным кошельком, который вы можете использовать, чтобы беспрепятственно оплачивать услуги Proto и делать больше с вашим приложением Proto!",

          smallHeading: "Оплата услуг Прото и многое другое.",
          smallHeadingP:
            "нет необходимости иметь дело с наличными деньгами и мелочами",
          smallHeading2: "Зарабатывайте очки ProtoRewards.",
          smallHeading2P:
            "для ProtoFood, в магазине и онлайн-транзакций, совершаемых с помощью ProtoPay.",
        });

        setImage1(protopayIcon);
        setImage2(protopayIcon2);
        setImgStyle({ maxWidth: "10rem" });
        setImgStyle2({ maxWidth: "10rem" });
        break;
      case "gifts":
        setInfo({
          heading: "Устали от мелочей каждый раз, когда вы делаете платеж?",
          heading2:
            "Сделайте его еще более полезным с ProtoPay - мобильным кошельком, который вы можете использовать, чтобы беспрепятственно оплачивать услуги Proto и делать больше с вашим приложением Proto!",

          smallHeading: "Оплата услуг Прото и многое другое.",
          smallHeadingP:
            "нет необходимости иметь дело с наличными деньгами и мелочами",
          smallHeading2: "Зарабатывайте очки ProtoRewards.",
          smallHeading2P:
            "для ProtoFood, в магазине и онлайн-транзакций, совершаемых с помощью ProtoPay.",
        });

        setImage1(protopayIcon);
        setImage2(protopayIcon2);
        setImgStyle({ maxWidth: "10rem" });
        setImgStyle2({ maxWidth: "10rem" });
        break;
      default:
    }
  }, []);

  return (
    <Fragment>
      <Navbar />
      <div
        style={{ minHeight: "34rem" }}
        className="container p-3 p-lg-5 p-xl-5"
      >
        <div className="text-center">
          <h1 className="my-3 m-lg-5 my-xl-5 heading-green">{info.heading}</h1>
          <h2 className="services-h2">{info.heading2}</h2>
          <div className="row">
            <div className="col-12 mx-auto col-sm-6 col-md-6 col-lg-6 col-xl-6">
              <img alt="img" className="my-2" style={imgstyle} src={image1} />
              <h3
                style={{ minHeight: "3.125rem" }}
                className="my-2 heading-green"
              >
                {info.smallHeading}
              </h3>
              <p
                style={{ minHeight: "3.125rem" }}
                className="my-2 heading-green"
              >
                {info.smallHeadingP}
              </p>
            </div>
            <div className="col-12 mx-auto col-sm-6 col-md-6 col-lg-6 col-xl-6">
              <img alt="img" className="my-2" style={imgstyle2} src={image2} />
              <h3
                style={{ minHeight: "3.125rem" }}
                className="my-2 heading-green"
              >
                {info.smallHeading2}
              </h3>
              <p
                style={{ minHeight: "3.125rem" }}
                className="my-2 heading-green"
              >
                {info.smallHeading2P}
              </p>
            </div>
          </div>
        </div>
      </div>
      <DowloadApp />
      <Prefooter />
      <Footer />
    </Fragment>
  );
};

export default SpecificService;
