import React, { Fragment, useState } from "react";

import Navbar from "./layout/Navbar";
import Prefooter from "./layout/Prefooter";
import Footer from "./layout/Footer";

import freedomIcon from "../assets/img/icons/Freedom.png";
import opportunityIcon from "../assets/img/icons/Opportunities.png";
import payoutIcon from "../assets/img/icons/Payout.png";
import safetyIcon from "../assets/img/icons/Safety.png";

const BecomeDeliveryResitration = () => {
  const [name, setName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");
  const [city, setCity] = useState("");

  const onNameChange = (e) => {
    setName(e.target.value);
  };

  const onLastnameChange = (e) => {
    setLastName(e.target.value);
  };

  const onEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const onPasswordChange = (e) => {
    setPassword(e.target.value);
  };
  const onPhoneChange = (e) => {
    setPhone(e.target.value);
  };
  const onCityChange = (e) => {
    setCity(e.target.value);
  };

  const onSubmit = (e) => {
    e.preventDefault();
    const data = {
      registerDeliveryData: {
        name,
        lastName,
        email,
        password,
        phone,
        city,
      },
    };
    console.log(data);
    clearFields();
  };
  const clearFields = () => {
    setName("");
    setLastName("");
    setEmail("");
    setPassword("");
    setCity("");
    setPhone("");
  };
  return (
    <Fragment>
      <Navbar />
      <section id="driver-register-form" className="driver-register-wrapper">
        <div
          id="driver-register-form-wrapper"
          className="container-fluid p-4 p-md-3 p-lg-5 p-xl-5 d-flex justify-content-around"
        >
          <div id="becomedriver-heading-wrapper" className="w-25 pt-5">
            <div className="mb-3 mb-md-0 mb-lg-0 mb-xl-0">
              <h1 className="text-black">Водитель доставки PROTO</h1>
              <h3>
                Получайте дополнительный доход, доставляя еду, посылки и
                предоставляя другие услуги консьержа.
              </h3>
            </div>
          </div>
          <div className="card w-55 p-2 p-md-3 p-lg-3 p-xl-5">
            <form className="p-3 p-md-0 p-lg-0 p-xl-0" onSubmit={onSubmit}>
              <div className="my-3">
                <h4 className="heading-green">Зарегистрируйтесь сейчас!</h4>
                <p className="heading-green">Введите вашу информацию.</p>
              </div>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <label htmlFor="inputName4">Имя</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputName4"
                    placeholder="Имя"
                    value={name}
                    onChange={onNameChange}
                  />
                </div>
                <div className="form-group col-md-6">
                  <label htmlFor="inputLastname4">Фамилия</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputLastname4"
                    placeholder="Фамилия"
                    value={lastName}
                    onChange={onLastnameChange}
                  />
                </div>
              </div>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <label htmlFor="inputEmail4">Электронная почта</label>
                  <input
                    type="email"
                    className="form-control"
                    id="inputEmail4"
                    placeholder="@почта"
                    value={email}
                    onChange={onEmailChange}
                  />
                </div>
                <div className="form-group col-md-6">
                  <label htmlFor="inputPassword4">Пароль</label>
                  <input
                    type="password"
                    className="form-control"
                    id="inputPassword4"
                    placeholder="пароль"
                    value={password}
                    onChange={onPasswordChange}
                  />
                </div>
              </div>
              <div className="form-group">
                <label htmlFor="inputPhone">Номер телефона</label>
                <input
                  type="text"
                  className="form-control"
                  id="inputPhone"
                  placeholder="телефон"
                  value={phone}
                  onChange={onPhoneChange}
                />
              </div>

              <div className="input-group mb-3">
                <select
                  className="custom-select"
                  id="inputGroupSelect02"
                  value={city}
                  onChange={onCityChange}
                >
                  <option defaultValue>Выбираете...</option>
                  <option value="Ташкент">Ташкент</option>
                  <option value="Самарканд">Самарканд</option>
                  <option value="Андижан">Андижан</option>
                </select>
                <div className="input-group-append">
                  <label
                    className="input-group-text"
                    htmlFor="inputGroupSelect02"
                  >
                    Город
                  </label>
                </div>
              </div>
              <div className="form-group">
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    id="gridCheck"
                    required={true}
                  />
                  <label className="form-check-label" htmlFor="gridCheck">
                    Я прочитал, понимаю и согласен с{" "}
                    <a href="/terms">политикой конфиденциальности Proto</a>
                  </label>
                </div>
              </div>
              <button type="submit" className="btn btn-block btn-success">
                Зарегистрироваться
              </button>
            </form>
          </div>
        </div>
        <div
          id="partner-driver-heading-whyjoin-wrapper"
          className="container w-100 p-2 p-md-5 p-lg-5 p-xl-5"
        >
          <div>
            <h2 className="partner-driver-heading mb-1 mb-xl-3">
              Зачем быть водителем доставки Proto?
            </h2>
            <p className="text-center mb-3 mb-xl-5">
              Вы сможете получать дополнительный доход и иметь свободу работать
              в свое свободное время.
            </p>
            <div>
              <div className="d-flex justify-content-center p-3 p-sm-4 p-md-4 p-lg-4 p-xl-4">
                <div>
                  <span>
                    <img
                      src={freedomIcon}
                      className="img-fluid"
                      style={{ maxWidth: "6rem" }}
                    />
                  </span>
                </div>
                <div className="mx-3">
                  <h4>СВОБОДА</h4>
                  <p style={{ maxWidth: "31rem" }} className="partner-driver-p">
                    Вы можете водить машину в любое время.Здесь нет неполного
                    или полного рабочего дня.
                  </p>
                </div>
              </div>
              <hr />
            </div>
            <div>
              <div className="d-flex justify-content-center p-3 p-sm-4 p-md-4 p-lg-4 p-xl-4">
                <div>
                  <span>
                    <img
                      src={opportunityIcon}
                      className="img-fluid"
                      style={{ maxWidth: "6rem" }}
                    />
                  </span>
                </div>
                <div className="mx-3">
                  <h4>БОЛЬШЕ ВОЗМОЖНОСТЕЙ</h4>
                  <p style={{ maxWidth: "31rem" }} className="partner-driver-p">
                    Иметь доступ к заказам для ProtoFood, ProtoExpress и
                    ProtoAssistant.Больше работы для вас. (Оплата при доставке)
                  </p>
                </div>
              </div>
              <hr />
            </div>

            <div>
              <div className="d-flex justify-content-center p-3 p-sm-4 p-md-4 p-lg-4 p-xl-4">
                <div>
                  <span>
                    <img
                      src={payoutIcon}
                      className="img-fluid"
                      style={{ maxWidth: "6rem" }}
                    />
                  </span>
                </div>
                <div className="mx-3">
                  <h4>БЫСТРЫЕ ВЫПЛАТЫ</h4>
                  <p style={{ maxWidth: "31rem" }} className="partner-driver-p">
                    Вы сможете получить выплаты к следующему дню, не упустите
                    возможность.
                  </p>
                </div>
              </div>
              <hr />
            </div>

            <div>
              <div className="d-flex justify-content-center p-3 p-sm-4 p-md-4 p-lg-4 p-xl-4">
                <div>
                  <span>
                    <img
                      src={safetyIcon}
                      className="img-fluid"
                      style={{ maxWidth: "6rem" }}
                    />
                  </span>
                </div>
                <div className="mx-3">
                  <h4>БЕЗОПАСНОСТЬ</h4>
                  <p style={{ maxWidth: "31rem" }} className="partner-driver-p">
                    Мы предоставляем обучение безопасности и страхование от
                    несчастных случаев во время бронирования.
                  </p>
                </div>
              </div>
              <hr />
            </div>
          </div>
        </div>
        <div
          id="why-wait-container"
          className="container-fluid d-flex justify-content-center p-2 p-md-3 p-lg-5 p-xl-5"
        >
          <div id="why-wait-container-inside-wrapper">
            <h2 className="my-2 my-md-3 my-lg-5 my-xl-5">Зачем ждать?</h2>
            <div>
              <a
                className="text-capitalize become-driver-reg-btn-up"
                href="#driver-register-form"
              >
                Зарегистрируйтесь
              </a>
            </div>
          </div>
        </div>
      </section>
      <Prefooter />
      <Footer />
    </Fragment>
  );
};

export default BecomeDeliveryResitration;
