import React, { Fragment } from "react";
import Navbar from "./layout/Navbar";
import Prefooter from "./layout/Prefooter";
import Footer from "./layout/Footer";

import carIcon from "../assets/img/icons/grabcar.png";
import freedomIcon from "../assets/img/icons/freedom_with_stroke.png";
import certaintyIcon from "../assets/img/icons/icon-certainty-1.png";
import safetyIcon from "../assets/img/icons/icon-safety-3.png";
import { Link } from "react-router-dom";

import { GiPlainArrow } from "react-icons/gi";
import DownloadApp from "./layout/DownloadApp";

const BecomeDriver = () => {
  return (
    <Fragment>
      <Navbar />
      <div
        id="partner-driver-wrapper"
        className="container-fluid p-5 d-flex justify-content-center"
      >
        <div>
          <h2 className="partner-driver-heading mb-5 mb-xl-5">
            Зачем становиться нашим партнером?
          </h2>
          <div>
            <div className="d-flex justify-content-center">
              <span>
                <img
                  src={safetyIcon}
                  style={{ width: "4rem" }}
                  className="img-fluid"
                  alt="img"
                />
              </span>
              <div className="mx-3">
                <h4>БЕЗОПАСНОСТЬ</h4>
                <p style={{ maxWidth: "31rem" }} className="partner-driver-p">
                  Имейте душевное спокойствие, зная, кто едет в вашей машине.
                </p>
              </div>
            </div>
            <hr />
          </div>
          <div>
            <div className="d-flex justify-content-center">
              <span>
                <img
                  src={certaintyIcon}
                  style={{ width: "4rem" }}
                  className="img-fluid"
                  alt="img"
                />
              </span>

              <div className="mx-3">
                <h4>Oпределенность</h4>
                <p style={{ maxWidth: "31rem" }} className="partner-driver-p">
                  Зарабатывайте дополнительные деньги, используя то, что у вас
                  уже есть, ваш автомобиль
                </p>
              </div>
            </div>
            <hr />
          </div>

          <div>
            <div className="d-flex justify-content-center">
              <span>
                <img
                  src={freedomIcon}
                  style={{ width: "4rem" }}
                  className="img-fluid"
                  alt="img"
                />
              </span>

              <div className="mx-3">
                <h4>СВОБОДА</h4>
                <p style={{ maxWidth: "31rem" }} className="partner-driver-p">
                  Отдавайте свое время, будьте своим собственным боссом.
                </p>
              </div>
            </div>
            <hr />
          </div>
        </div>
      </div>
      <div className="container p-1 p-md-2 p-lg-2 p-xl-2 my-1 my-sm-2 my-lg-2 my-xl-2">
        <div className="text-center my-1">
          <h2 className="heading-green partner-driver-2nd-heading">
            Каким типом водителя вы можете стать?
          </h2>
        </div>
        <div className="row">
          <div className="col-12 p-3 mx-auto col-md-6 col-lg-6 col-xs-6 d-flex flex-column">
            <div className="align-self-center">
              <img
                src={carIcon}
                className="img-fluid"
                style={{ maxHeight: "90px", maxWidth: "90px" }}
                alt="car"
              />
              <h4>Машина</h4>
            </div>
            <div className="align-center">
              <p
                className="p-1 text-center"
                style={{ minWidth: "6rem", minHeight: "5rem" }}
              >
                Простой и гибкий способ увеличить ваш доход
              </p>
              <div className="text-center">
                <Link
                  className="become-driverpartner-link"
                  to="/become-driver-register"
                >
                  Узнать больше
                </Link>
              </div>
            </div>
          </div>

          <div className="col-12 p-3 mx-auto col-md-6 col-lg-6 col-xs-6 d-flex flex-column">
            <div className="align-self-center">
              <img
                src={carIcon}
                className="img-fluid"
                style={{ maxHeight: "90px", maxWidth: "90px" }}
                alt="car"
              />
              <h4>Экспресс</h4>
            </div>
            <div className="text-center">
              <p
                className="p-1"
                style={{ minWidth: "6rem", minHeight: "5rem" }}
              >
                Отправить вещи легко и быстро
              </p>
              <div className="text-center">
                <Link
                  className="become-driverpartner-link"
                  to="/become-delivery-register"
                >
                  Узнать больше
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container p-2 p-md-3 p-lg-5 p-xl-5 d-flex flex-column">
        <div className="align-self-center">
          <h2>Как это работает?</h2>
          <p>Получите пассажиров всего за 4 шага!</p>
        </div>

        <div className="row d-flex flex-column justfiy-content-center">
          <div className="col-12 d-flex flex-column p-2 p-md-3 p-lg-4 p-xl-4">
            <div className="align-self-center">
              <div className="text-center">
                <div className="mx-auto circle-count">
                  <GiPlainArrow />
                </div>
              </div>
              <div className="text-center">
                <h4>Получите работу</h4>
                <p>
                  Запустите приложение, чтобы начать получать заказы пассажиров
                </p>
              </div>
            </div>
          </div>
          <div className="col-12 d-flex flex-column p-2 p-md-3 p-lg-4 p-xl-4">
            <div className="align-self-center">
              <div className="text-center">
                <div className="mx-auto circle-count">
                  <GiPlainArrow />
                </div>
              </div>
              <div className="text-center">
                <h4>Принимаете бронирование.</h4>
                <p>
                  Примите бронирование, чтобы получить данные вашего пассажира
                </p>
              </div>
            </div>
          </div>
          <div className="col-12 d-flex flex-column p-3 p-md-3 p-lg-4 p-xl-4">
            <div className="align-self-center">
              <div className="text-center">
                <div className="mx-auto circle-count">
                  <GiPlainArrow />
                </div>
              </div>
              <div className="text-center">
                <h4>Подбирате пассажира.</h4>
                <p>
                  Система навигации GPS покажет местоположение пассажира,
                  который будет подобран
                </p>
              </div>
            </div>
          </div>
          <div className="col-12 d-flex flex-column p-2 p-md-3 p-lg-4 p-xl-4">
            <div className="align-self-center">
              <div className="text-center">
                <div className="mx-auto circle-count">
                  <GiPlainArrow />
                </div>
              </div>
              <div className="text-center">
                <h4>довезти до адресa.</h4>
                <p>
                  Выберите место высадки, как только вы достигнете пункта
                  назначения, заберите свой тариф.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <DownloadApp />
      <Prefooter />
      <Footer />
    </Fragment>
  );
};

export default BecomeDriver;
