import React from "react";
import { Link } from "react-router-dom";

import transportImg from "../../assets/img/logos/01-icon-transport-700x700.png";
import foodImg from "../../assets/img/logos/02-icon-food-700x700.png";
import payImg from "../../assets/img/logos/15-icon-pay-700x700.png";
import deliveryImg from "../../assets/img/logos/03-icon-delivery-700x700.png";
import airtimeImg from "../../assets/img/logos/05-icon-airtime-700x700.png";
import billsImg from "../../assets/img/logos/bills-700x700.png";
import rewardsImg from "../../assets/img/logos/11-icon-rewards-700x700.png";
import giftImg from "../../assets/img/logos/icon-grabgift-700x700.png";

import iosBtn from "../../assets/img/logos/app-store-btn.png";
import androidBtn from "../../assets/img/logos/play-store-btn.png";
import { FaAppStore } from "react-icons/fa";
const Services = () => {
  return (
    <section className="section-services">
      <div className="container p-5">
        <h2 className="services-big-heading my-5">
          Узнайте больше о том, как мы помогаем вам удовлетворить ваши
          повседневные потребности.
        </h2>
        <div className="row">
          <div className="card-deck my-3">
            <div className="card">
              <Link to="/services/cab">
                <img
                  className="card-img-top"
                  src={transportImg}
                  alt="transport img"
                />
                <div className="card-body">
                  <h5 className="card-title">Транспорт</h5>
                  <p
                    className="card-text p-1"
                    style={{ minHeight: "3.125rem" }}
                  >
                    Перенесет вас от А до Б с начальными ценами.
                  </p>
                </div>
              </Link>
            </div>
            <div className="card">
              <Link to="/services/food">
                <img
                  className="card-img-top"
                  src={foodImg}
                  alt="Card image cap"
                />
                <div className="card-body">
                  <h5 className="card-title">Еда</h5>
                  <p
                    className="card-text p-1"
                    style={{ minHeight: "3.125rem" }}
                  >
                    Заказывайте еду, когда вы слишком заняты (или устали), чтобы
                    выходить на улицу.
                  </p>
                </div>
              </Link>
            </div>
            <div className="card">
              <Link to="/services/pay">
                <img
                  className="card-img-top"
                  src={payImg}
                  alt="Card image cap"
                />
                <div className="card-body">
                  <h5 className="card-title">Оплачивать счета и т. Д.</h5>
                  <p
                    className="card-text p-1"
                    style={{ minHeight: "3.125rem" }}
                  >
                    Помогает вам производить безналичные платежи, безопасно,
                    быстро и надежно.
                  </p>
                </div>
              </Link>
            </div>
            <div className="card">
              <Link to="/services/delivery">
                <img
                  className="card-img-top"
                  src={deliveryImg}
                  alt="Card image cap"
                />
                <div className="card-body">
                  <h5 className="card-title">Доставка</h5>
                  <p
                    className="card-text p-1"
                    style={{ minHeight: "3.125rem" }}
                  >
                    Получает ваши документы или пакеты в пункт назначения по
                    запросу.
                  </p>
                </div>
              </Link>
            </div>
          </div>

          <div className="card-deck">
            <div className="card">
              <Link to="/services/airtime">
                <img
                  className="card-img-top"
                  src={airtimeImg}
                  alt="transport img"
                />
                <div className="card-body">
                  <h5 className="card-title">Эйртайм</h5>
                  <p
                    className="card-text p-1"
                    style={{ minHeight: "3.125rem" }}
                  >
                    Помогает вам оставаться на связи с помощью быстрых
                    пополнений.
                  </p>
                </div>
              </Link>
            </div>
            <div className="card">
              <Link to="/services/bills">
                <img
                  className="card-img-top"
                  src={billsImg}
                  alt="Card image cap"
                />
                <div className="card-body">
                  <h5 className="card-title">Oплату Cчетов</h5>
                  <p
                    className="card-text p-1"
                    style={{ minHeight: "3.125rem" }}
                  >
                    Делает оплату счетов меньше рутинной работы.
                  </p>
                </div>
              </Link>
            </div>
            <div className="card">
              <Link to="/services/rewards">
                <img
                  className="card-img-top"
                  src={rewardsImg}
                  alt="Card image cap"
                />
                <div className="card-body">
                  <h5 className="card-title">Награды</h5>
                  <p
                    className="card-text p-1"
                    style={{ minHeight: "3.125rem" }}
                  >
                    Позволяет вам зарабатывать баллы и открывать преимущества с
                    каждой транзакцией PROTO.
                  </p>
                </div>
              </Link>
            </div>
            <div className="card">
              <Link to="/services/gifts">
                <img
                  className="card-img-top"
                  src={giftImg}
                  alt="Card image cap"
                />
                <div className="card-body">
                  <h5 className="card-title">Подарки</h5>
                  <p
                    className="card-text p-1"
                    style={{ minHeight: "3.125rem" }}
                  >
                    Пошлите забавные, но практичные подарки PROTO семье и
                    друзьям.
                  </p>
                </div>
              </Link>
            </div>
          </div>
        </div>
        <div
          id="services-getappbtn-container"
          className="my-5 d-flex flex-column"
        >
          <p className="services-downloadApp-heading">Скачать приложение</p>
          <div className="d-flex justify-content-start">
            <div>
              <button className="service-section-get-app-btn">
                <img src={iosBtn} className="img-fluid" />
              </button>
            </div>
            <div>
              <button className="service-section-get-app-btn">
                <img src={androidBtn} className="img-fluid" />
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Services;
