import React, { Fragment } from "react";
import DownloadApp from "./DownloadApp";
import Navbar from "./Navbar";
import Prefooter from "./Prefooter";
import Footer from "./Footer";

const Contact = () => {
  return (
    <Fragment>
      <Navbar />
      <div className="container p-2 p-lg-5 p-xl-5">
        <div className="text-center">
          <h1 className="heading-green my-3">КАК МЫ МОЖЕМ ВАМ ПОМОЧЬ?</h1>
          <p className="heading-green my-3">
            Вы можете связаться с нами через наше приложение или позвонить по
            телефону +9989755564433
          </p>
        </div>
      </div>
      <DownloadApp />
      <Prefooter />
      <Footer />
    </Fragment>
  );
};

export default Contact;
